import React from 'react';
import './video-detalle.css';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from '@fortawesome/free-solid-svg-icons';

export default class VideoDetalle extends React.Component{
    render(){
        return(
            <div>
                <div className="video-detalle ml-4">
                    <div className="portada-video-detalle ml-0">
                        <img
                            src="data:image/jpeg;base64,"
                            alt=""
                        />
                       <span><FontAwesomeIcon icon={faPlay} color="white" size="5x" /></span> 
                    </div>
                    <div className="detalle-video-detalle">
                        <div className="contenido">
                            <div className="row">
                            <div className="bold-white-22"><p>Adele</p></div>
                            </div>
                            <div className="row">
                            <div className="d-inline regular-white-14">
                                <label className="mr-4">Lo mejor de adele</label>
                                <label className="seguidores">312,143 seguidores</label>
                            </div>
                            </div>
                            <div className="row">
                            <p className="detalle regular-white-lineheight-20-paragraph-12">Adele Laurie Blue Adkins (Tottenham, Londres, Inglaterra, 5 de mayo de 1988), conocida simplemente como Adele, es una cantante, compositora y multinstrumentista británica.8​ </p>
                            </div>
                            <div className="row align-items-end">
                            <div className="d-inline regular-white-14">
                                <button className="btn reproducir mr-2">Reproducir</button> 
                                <button className="btn seguir mr-2">Seguir</button> 
                                <button className="btn mas mb-2">...</button> </div>
                            </div>

                        </div>
                            
                    </div>
                </div>
            </div>
        );
    }
}